<template>
    <h1 v-if="!pokemon">Cargando pokemon....</h1>

    <div v-else>
        <pokemonPicture :pokemonId="pokemon.id" :showPokemon="showPokemon" />
        <pokemonOptions :pokemons="pokemonArr" @selectionPokemon="checkAnswer( $event )" />

        <div class="resultado" v-if="showAnswer">
            <h2>{{ message }}</h2>
            <button @click="nuevoGame">Nuevo juego</button>
        </div>
    </div>

</template>

<script>
import PokemonPicture from "@/components/PokemonPicture.vue";
import PokemonOptions from "@/components/PokemonOptions.vue";

import getPokemonOptions from '@/helpers/getPokemonOptions'


export default {

    components: {
        PokemonPicture,
        PokemonOptions
    },

    data() {
        return {
            pokemonArr: [],
            pokemon: null,
            showPokemon: false,
            showAnswer: false,
            message: ''
        }
    },

    methods: {
        async mixPokemonArray() {
            this.pokemonArr = await getPokemonOptions()

            const rndInt = Math.floor( Math.random() * 4 )
            this.pokemon = this.pokemonArr[ rndInt ]
        },

        checkAnswer( selectedId ) {
            this.showPokemon = true
            this.showAnswer = true
         
            if( selectedId === this.pokemon.id ) {
                this.message = `Correcto, ${this.pokemon.name}`
            } else {
                this.message = `Nooo, era ${this.pokemon.name}`
            }
        },

        nuevoGame () {
            this.showPokemon = false
            this.showAnswer = false
            this.pokemonArr = []
            this.pokemon = null
            this.mixPokemonArray()
        }
    },

    mounted() {
        this.mixPokemonArray()
    }

}
</script>

<style scoped>

    .resultado{
        position: absolute;
        margin-top: -43%;
        margin-left: 60%;
        font-size: 2rem;
        color: #ffcc03;
    }

    .resultado button{

        background-color: #ffcc03;
        color: black;
        border-color: #ffcc03;
        width: 10rem;
        border-radius: 5px;
        cursor: pointer;
        font-size: 20px;

    }

    .resultado button:hover{

        background-color: black;
        color: #ffcc03;
        border-color: black;
        width: 10rem;

    }

    @media (min-height: 900px) {
        
        .resultado{
            margin-top: -38% ;
        }
        
    }

    @media (max-width: 1700px){

        .resultado{
            margin-top: -45%;
        }

    }


    @media (max-width: 830px){

        .resultado{
            margin: 0 0 0 0;
        }

    }

</style>