<template>
    <div class="pokemon-container">
        <img :src="imgSrc" 
            class="hidden-pokemon"
            alt="pokemon">
        <img v-if="showPokemon" :src="imgSrc" 
            class="fade-in"
            alt="pokemon">
    </div>
    
</template>

<script>

export default {

    props: {
        pokemonId: {
            type: Number,
            required: true
        },
        showPokemon: {
            type: Boolean,
            required: true,
            default: false
        }
    },

    computed: {

        imgSrc() {
            return `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/other/dream-world/${ this.pokemonId }.svg`
        }

    }

}
</script>

<style scoped>

    .pokemon-container {
        height: 400px;
    }

    img {
        height: 400px;
        position: absolute;
        width: 100vw;
        margin-left: -75vw;
        user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        -webkit-user-drag: none;
        -webkit-user-select: none;
    }

    @media (min-width: 1500px) {
        .pokemon-container {
            height: 500px;
        }

        img{
            height: 500px;
        }
    }

    @media (max-width: 600px){

        .pokemon-container {
            height: 300px;
        }

        img{
            height: 300px;
            margin-left: -50vw;
        }

    }

    .hidden-pokemon {
        filter: brightness(0);
    }

</style>